* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0; }
a {
  text-decoration: none; }
.red {
  color: $primary; }
.grey {
  color: $muted; }

//mixin
@mixin clearFix() {
  &:before,
  &:after {
    content: " ";
    display: table; }

  &:after {
    display: block;
    clear: both;
    height: 1px;
    margin-top: -1px;
    visibility: hidden; }
  & {
    *zoom: 1; } }

//oontainer
.content-container {
  width: 60%;
  margin-left: 25vw;
  margin-top: 15vh;
  height: auto;
  @media screen and (max-width: 1500px) {
    width: 70%;
    margin-left: 22vw; }
  @media screen and (max-width: $tablet) {
    width: 90%;
    margin-left: 5vw;
    margin-top: 80px; }
  @media screen and (max-width: $mobile) {
    margin-top: 50px; }
  @media screen and (max-width: 340px) {
    margin-top: 10px; } }

// character
.chara-fixed {
  position: fixed;
  z-index: -1000;
  @media screen and (max-width: $tablet) {
    display: none; }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain; } }
.chara-left {
  bottom: 20px;
  left: 20px;
  width: 250px;
  height: 250px;
  @media screen and (max-width: 1500px) {
    width: 200px;
    height: 200px;
    left: 10px;
    bottom: 10px; }
  @media screen and (max-width: 1200px) {
    width: 150px;
    height: 150px; } }


.chara-right {
  top: 20px;
  right: 0;
  width: 260px;
  height: 260px;
  @media screen and (max-width: 1500px) {
    width: 210px;
    height: 210px;
    right: -30px; }
  @media screen and (max-width: 1200px) {
    width: 150px;
    height: 150px;
    right: -30px; } }

//next prev button
.scroll-btn-container {
  width: 50px;
  height: 200px;
  position: fixed;
  top: 70%;
  right: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @media screen and (max-width: 1500px) {
    right: 40px;
    top: 75%;
    width: 40px;
    height: 150px; }
  @media screen and (max-width: $tablet) {
    display: none; }
  .scroll-btn-wrap {
    width: 100%;
    img {
      width: 100%;
      transition: all 0.5s ease; } }
  .scroll-btn-wrap.up {
    &:hover {
      img {
       transform: translateY(-15px); } } }
  .scroll-btn-wrap.down {
    &:hover {
      img {
       transform: translateY(15px); } } } }


.for-desk {
  @media screen and (max-width: $tablet) {
    display: none; } }
.for-mobile {
  display: none;
  @media screen and (max-width: $tablet) {
    display: block; } }

.scroll-btn-anim {
  animation-duration: 1.8s;
  animation-name: iniAnimation;
  transform-origin: center bottom; }
