.about-link {
  color: $primary;
  text-decoration: none;
  border-bottom: 1px solid $primary;
  padding-bottom: 5px; }

.about-container {
  display: flex;
  @media screen and (max-width: 600px) {
    flex-direction: column; }

  .about-left {
    width: 35%;
    @media screen and (max-width: 600px) {
      width: 100%;
      margin-bottom: 30px; }
    img {
      width: 100%; } }
  .email {
    margin-top: 20px;
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    .envelope {
      width: 30px;
      height: 20px;
      margin-top: -12px; }
    .email-text {
      font-size: 1.7rem;
      color: $primary;
      text-decoration: none;
      padding-left: 10px;
      display: inline-block; } }

  .about-right {
    width: 65%;
    padding-left: 30px;
    @media screen and (max-width: 600px) {
      width: 100%;
      padding-left: 0; }
    .about-title {
      font-size: 2.5rem;
      margin: 0;
      margin-top: -10px; }
    .about-text {
      margin-top: 0;
      @media screen and (max-width: 600px) {
        font-size: 1.2rem; } }
    .about-title.about-news {
      margin-top: 40px; }
    .about-text.about-news {
      @media screen and (max-width: 600px) {
        margin-bottom: 200px; } } } }

//character
.chara-fixed.chara-left.about {
  width: 260px;
  height: 260px; }

.chara-fixed.chara-right.about {
  width: 280px;
  height: 280px; }

.news-btn {
  display: none;
  width: 100%;
  background: $head;
  text-align: center;
  margin-bottom: 50px;
  @media screen and (max-width: 600px) {
    display: block; }
  .news-btn-text {
    font-size: 1.4rem;
    display: block;
    color: white;
    font-family: "Franklin_demi"; } }
