.home-video-container {
  width: 100%;
  height: 70vh;
  position: relative;
  overflow: hidden;
  @media screen and (max-width: 1500px) {
    width: 91%; }
  @media screen and (max-width: $tablet) {
    width: 100%;
    height: 500px; }
  @media screen and (max-width: $mobile) {
    height: 250px; }
  .greeting-container {
    position: absolute;
    bottom: -10px;
    right: -5px;
    width: 400PX;
    height: 350px;
    @media screen and (max-width: 1300px) {
      width: 350PX;
      height: 300px; }
    @media screen and (max-width: 1200px) {
      width: 320PX;
      height: 270px; }
    @media screen and (max-width: $tablet) {
      display: none; } } }


.greeting-contents {
  position: absolute;
  bottom: 100px;
  right: 100px;
  width: 460px;
  height: 350px;
  @media screen and (max-width: 1700px) {
    bottom: 60px;
    right: 60px; }
  @media screen and (max-width: 1500px) {
    right: 30px;
    bottom: 50px; }
  @media screen and (max-width: 1300px) {
    bottom: -20px;
    right: 20px;
    width: 400px; }
  @media screen and (max-width: 1200px) {
    bottom: -50px;
    right: 30px;
    width: 380px; }
  @media screen and (max-width: 1100px) {
    right: 10px; }
  @media screen and (max-width: $tablet) {
    position: static;
    width: 70%;
    height: auto;
    margin-top: 30px;
    margin-bottom: 50px; }
  @media screen and (max-width: $mobile) {
    width: 100%;
    margin-top: 10px; } }



//welcome contents
.welcome {
  font-size: 3rem;
  margin: 0;
  @media screen and (max-width: 1300px) {
    font-size: 2.5rem;
    margin-bottom: -10px; }
  @media screen and (max-width: $tablet) {
    font-size: 4rem; }
  @media screen and (max-width: $mobile) {
    font-size: 2.5rem; } }
.welcome-words {
  font-size: 1.3rem;
  margin: 0;
  @media screen and (max-width: 1300px) {
    font-size: 1.1rem; }
  @media screen and (max-width: $tablet) {
    font-size: 1.5rem; }
  @media screen and (max-width: $mobile) {
    font-size: 1.2rem; } }
.welcome-intro {
  padding-top: 10px;
  font-size: 1rem;
  margin-bottom: 20px;
  @media screen and (max-width: 1200px) {
    font-size: 0.9rem; }
  @media screen and (max-width: $tablet) {
    font-size: 1.2rem; }
  @media screen and (max-width: $mobile) {
    font-size: 1rem; } }

.home-video {
  width: 100%;
  height: 100%;
  object-fit: cover; }


//character
.chara-fixed.chara-right.home {
  width: 300px;
  height: 300px;
  right: -30px;
  @media screen and (max-width: 1500px) {
    width: 250px;
    height: 250px;
    right: -30px; }
  @media screen and (max-width: 1300px) {
    width: 200px;
    height: 200px; } }
